import React,{useState,useEffect} from 'react';
import { BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import {history} from '../_helpers';
import 'bootstrap/dist/css/bootstrap.min.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import {Preloader} from '../components';
import {Home,HowWorks,CreateProject,DeveloperTeam,PageNews,ChartsPage,LoginHome,RegisterHome,RegisterStudent,RegisterClient,Login,ApplicationStudents,Project,Help,ProjectProposer
,Pool,ClientPool} from '../pages';


const MainRouter = () =>{
    
    const [loading,setLoading] = useState(true);

    useEffect(() =>{
        setTimeout(()=>{
            setLoading(false);
        },500)
    },[]);

    if(loading){
        return <Preloader/>
    }
    return(
        <Router history={history}>
            <Switch>
                <Route exact path="/SPOOL/student/pool" component={Pool}/>
                <Route exact path="/SPOOL/student/project" component={Project}/>
                <Route path="/SPOOL/student/applications" component={ApplicationStudents}/>

                <Route path="/SPOOL/client/charts" component={ChartsPage}/>
                <Route exact path="/SPOOL/client/project" component={ProjectProposer}/>
                <Route path="/SPOOL/client/newproject" component={CreateProject}/>
                <Route exact path="/SPOOL/client/pool" component={ClientPool}/>

                <Route path="/SPOOL/register/student" component={RegisterStudent}/>
                <Route path="/SPOOL/register/client" component={RegisterClient}/>
                <Route path="/SPOOL/register" component={RegisterHome}/>
                <Route path="/SPOOL/login/home" component={LoginHome}/>
                <Route path="/SPOOL/login" component={Login}/>

                <Route exact path="/SPOOL/help" component={Help}/>
                <Route path="/SPOOL/how-start" component={HowWorks}/>
                <Route path="/SPOOL/our-team" component={DeveloperTeam}/>
                <Route path="/SPOOL/news" component={PageNews}/>
                <Route exact path="/SPOOL/pool" component={Pool}/>
                <Route path="/" component={Home}/>
                
            </Switch>
        </Router>
    )
}

export default MainRouter;